.lanting-nav {
  &-inner {
    max-width: 100%;
    width: 56rem;
    margin: 0 auto;
    height: 3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &-title {
    display: flex;
    align-items: flex-end;
    line-height: 1;
  }

  &-link {
    text-decoration: none;
    color: var(--primary-text);
  }

  &-name {
    margin: 0;
  }

  &-bio {
    color: var(--secondary-text);
    margin-left: 0.5rem;
    font-size: 0.875rem;
  }
}
